<template>

<div class="home-quote-page">

    <div class="home-quote-text">
        <div ref="quote" class="quote-p">
            Culture, like science and technology, grows by accretion, each new creator building <br> on the works of those who came before. 
        <br>
        <br>
            <div class="quoteTwo">
                Creativity is impossible without a <br> rich public domain.
                <span class="home-quote-speaker" ref="speaker">Alex Kozinski</span>

            </div>
        </div>
       <router-link  to="/home"> <div ref="link" class="home-quote-enter-link">Enter</div></router-link>
    </div>
